footer {
  background: var(--primary-blue);
  position: relative;
  z-index: 2;
  color: white;
  //   margin-top: 179px;

  & .footer-center {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    padding: 83px 0;
    // display: flex;
    justify-content: center;
    // align-items: center;

    & .item {
      margin: 0 auto;
      display: flex;
      flex-direction: column;
      gap: 30px;

      & h2 {
        font-family: "Open Sans";
        font-style: normal;
        font-weight: 600;
        font-size: 30px;
        line-height: 41px;
        text-transform: capitalize;
      }

      & .bottom {
        display: flex;
        flex-direction: column;
        gap: 15px;

        & p {
          font-family: "Open Sans";
          font-style: normal;
          font-weight: 400;
          font-size: 18px;
          line-height: 27px;
          text-transform: capitalize;
          cursor: pointer;
          display: flex;
          align-items: center;
          gap: 20px;
        }
      }

      & .input {
        display: flex;
        input {
          padding: 12px 15px;
          font-family: "Open Sans";
          font-style: normal;
          font-weight: 400;
          font-size: 18px;
          line-height: 27px;
          border-top-left-radius: 5px;
          border-bottom-left-radius: 5px;
          border: none;
          width: 193px;
        }
        B\HJU MIUJK & button {
          padding: 12px 22px;
          text-transform: uppercase;
          background: #ed1c24;
          font-family: "Open Sans";
          font-style: normal;
          font-weight: 800;
          border: none;
          font-size: 18px;
          cursor: pointer;
          line-height: 27px;

          color: #ffffff;
        }
      }

      & h3 {
        font-family: "Open Sans";
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 33px;
      }

      //   & icons{
      //                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                             a
      //   }
    }
  }
}
